/* Global Styles */

@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');


:root {
  --primary-color: #15c6ad;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Nunito', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}

a {
  color: var(--primary-color);
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Utilities */
.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
  margin-top: 6rem;
  margin-bottom: 3rem;
}

/* Text Styles*/
.x-large {
  /* color: var(--primary-color); */
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: var(--primary-color);
}

.text-dark {
  color: var(--dark-color);
}

/* Padding */
.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

/* Margin */
.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

.btn {
  display: inline-block;
  background: var(--light-color);
  color: #333;
  padding: 0.2rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.badge {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: center;
  margin: 0.3rem;
  background: var(--light-color);
  color: #333;
}

.alert {
  padding: 0.8rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: var(--light-color);
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff;
}


.btn-success,
.bg-success,
.badge-success,
.alert-success{
  background: var(--success-color);
  color: #fff;
}


.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
  border: #ccc solid 1px;
}

.small{
  font-size: .9rem;
}
.btn:hover {
  opacity: 0.8;
}

.bg-light,
.badge-light {
  border: #ccc solid 1px;
}

.round-img {
  border-radius: 50%;
}

.profile-img {
  height: 250px;
  width: 220px;
  object-fit: center;
}

.avatar-img {
  height: 150px;
  width: 130px;
  object-fit: center;
}
.line {
  height: 1px;
  background: #ccc;
  margin: 1.5rem 0;
}

/* Overlay */
.dark-overlay {
  background-color: rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Forms */
.form .form-group {
  margin: 1.2rem 0;
}

.form .form-text {
  display: block;
  margin-bottom: 0.3rem;
  color: var(--dark-color);
  font-size: 1.2rem;
}

.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form select,
.form textarea {

  display: block;
  width: 100%;
  font-family: 'Raleway', sans-serif;
  padding: 0.4rem;
  font-size: 1.1rem;
  color: var(--dark-color);
  border: 1px solid #ccc;
}

.form input[type='submit'],
button {
  font: inherit;
}

.form .contact-input {
  display: flex;
}

.form .contact-input i {
  padding: 0.5rem;
  width: 4rem;
}

.table th,
.table td {
  padding: 1rem;
  text-align: left;
}

.table th {
  background: var(--light-color);
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
  border-bottom: solid 1px var(--primary-color);
  opacity: 0.9;
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: #fff;
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: var(--primary-color);
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* Landing Page */
.landing {
  position: relative;
  background: url('./img/woman-cleaning-kitchen-horiz_vcumis\ \(1\).jpeg') no-repeat center center/cover;
  height: 100vh;
}

.landing-inner {
  color: #fff;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}



/* Search Bar */
.search {
  display: grid;
  grid-template-columns: 6fr 2fr;
  align-items: center;
  grid-gap: 1rem;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem;
}

/* Profiles Page */
.profile {
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  align-items: center;
  grid-gap: 2rem;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem;
}


/* Profile Page */
.profile-grid {
  display: grid;
  grid-template-areas:
    'top top'
    'about about'
    'exp edu';
  grid-gap: 1rem;
}

.profile-top {
  grid-area: top;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.profile-top img {
  width: 250px;
  margin-right: 100px;

}



/* .profile-top .profile-info{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center
} */

.profile-top .icons a {
  color: #fff;
  margin: 0 0.3rem;
}

.profile-top .icons a:hover {
  color: var(--dark-color);
}

.profile-about {
  grid-area: about;
  text-align: center;
}

.profile-about .skills {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.profile-exp {
  grid-area: exp;
}


.profile-exp h2,
.profile-edu h2 {
  margin-bottom: 1rem;
}

.profile-exp > div,
.profile-edu > div {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: #ccc 1px dotted;
}

.profile-exp > div:last-child,
.profile-edu > div:last-child {
  border: 0;
}

.profile-exp p,
.profile-edu p {
  margin: 0.5rem 0;
}

.profile-github {
  grid-area: github;
}

.profile-github .repo {
  display: flex;
}

.profile-github .repo > div:first-child {
  flex: 7;
  flex-basis: 70%;
}

.profile-github > div:last-child {
  flex: 3;
  flex-basis: 20%;
}

/* Posts Page */
.post-form .post-form-header {
  background: var(--primary-color);
  padding: 0.5rem;
}

.post {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-gap: 2rem;
  align-items: center;
}

.post > div:first-child {
  text-align: center;
}

.post img {
  width: 100px;
}

.post .comment-count {
  background: var(--light-color);
  color: var(--primary-color);
  padding: 0.1rem 0.2rem;
  border-radius: 5px;
  font-size: 0.8rem;
}

.post .post-date {
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

/* .rating{
  text-align: center;
} */

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  /* padding: 20px; */
}
.grid-item {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  font-size: 16px;
  text-align: center;
}

/* Mobile Styles */
@media (max-width: 700px) {
  .container {
    margin-top: 1.5rem;
    margin-bottom: 5rem
  }

  .landing{
    margin-bottom: -4rem;
  }

  .hide-sm {
    display: none;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1.2rem;
  }

  /* Navbar */
  .navbar {
    border-top: solid .1rem var(--primary-color);
    display: block;
    /* text-align: center; */
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar ul li{
    padding: 5px !important;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }

  .navbar i{
    font-size: 24px;
  }

  /* Profiles Page */
  .profile {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .profile ul {
    display: none;
  }

  /* Profile Page */

  .profile-img {
  height: 220px;
  width: 180px;
  object-fit: center;
}

.avatar-img {
  height: 180px;
  width: 150px;
  object-fit: center;
}

  .profile-top {
  flex-direction: column;
  text-align: center;
}
  .profile-top img,
  .profile img {
    width: 200px;
    height: 220px;
    object-fit: center;
  }

  .profile-grid {
    grid-template-areas:
      'top'
      'about'
      'exp'
      'edu'
      'github';
  }

  .profile-about .skills {
    flex-direction: column;
  }

   .dash-buttons{
    text-align: center;
  }

  .dash-buttons a {
    text-align: center;
    display: block;
    width: 50%;
    margin: 1rem auto;
  }

  .post {
    grid-template-columns: 1fr;
  }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem;
  }

  .grid-container {
  display: grid;
  grid-template-columns: auto auto;
  padding: 20px;
}
.form-text {
  font-size: 1rem !important;
  text-align: center;
}
.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form input[type='radio'],
.form select,
.form textarea {
  text-align: center;
  font-size: .8rem;
  
}
.avatar-img {
  height: 140px;
  width: 120px;
  object-fit: center;
}
.container {
  padding: 0 1rem;
}
.profile-top.p-2{
  padding: 1rem;
}

.text-primary, .lead, small, p, h2, h3, h4{
  text-align: center;
}

form{
text-align: center;
}

.btn.del{
  margin: 0 auto;
  width:60%;
  display: block;
}

.btn.create{
  margin: 1rem auto;
  width:50%;
  display: block;
  text-align: center;
}
.table th,
.table td {
  font-size: .9rem;
  padding: .7rem;

}

.rating{
  text-align: center;
}

.alert-container{
  position: fixed;
    z-index: 1000;
    top: 5rem;
    right: 1rem;
    width: 12rem;
    height: 8rem;
    padding: 0.5rem;
    opacity: 0.90;
    border: none;
    border-radius: 2px;
}

.alert-container-success:after{
  content: '';
  display: block;
      position: absolute;
      margin: -1.05rem 5rem;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 12px solid var(--success-color);
}

.alert-container-danger:after{
  content: '';
  display: block;
      position: absolute;
      margin: -1.05rem 5rem;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 12px solid var(--danger-color);
}

.alert-msg {
        margin-top: 0.5rem;
        font-size: 1rem;
        letter-spacing: 0.1rem;
        text-align: center;
      }

      .navbar{
        position: fixed;
    top: 91vh;
    height: 9vh;
    width: 100%;
    background-color: var(--dark-color);
      }

      .navbar ul{
        display: flex;
      justify-content: space-between;
      align-items: center;
      }

     
      .navbar ul li {
        display: inline-block;
        padding: 10px;
      }

      .landing-inner .lead{
  padding-top: 7rem;
}
}
